.WelcomePage {
  display: flex;
  height: 100vh;
}

.Sidebar {
  background-color: rgb(154, 150, 199);
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  padding-right: 6rem;
}
.Sidebar .head {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.Sidebar .head .user-image {
  width: 5rem;
  height: 5rem;
  background-color: grey;
  border-radius: 50%;
}
.Sidebar .head .user-details {
  display: flex;
  flex-direction: column;
  gap: 1px;
  font-size: 1.2rem;
}
.Sidebar .head .user-details span.name {
  font-weight: 600;
}
.Sidebar .head .user-details a {
  text-decoration: underline;
}
.Sidebar button.new-project-btn {
  background-color: rgb(27, 27, 75);
  border: none;
  border-radius: 0.4rem;
  color: white;
  padding: 0.7rem;
  text-align: start;
  padding-left: 3rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.Sidebar button.new-project-btn svg {
  color: white;
  font-size: 2rem;
}
.Sidebar nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-size: 1.5rem;
  padding: 2rem;
  padding-top: 1rem;
}
.Sidebar nav a {
  color: rgb(27, 27, 75);
  text-decoration: none;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.Sidebar nav a svg {
  font-weight: 600;
  font-size: 2rem;
}
.Sidebar nav a:hover {
  color: rgb(189, 189, 189);
}
.Sidebar nav a.active {
  color: rgb(255, 255, 255);
}
.Sidebar button.logout-btn {
  position: absolute;
  bottom: 4rem;
  background-color: transparent;
  border: none;
  border-radius: 0.4rem;
  color: rgb(27, 27, 75);
  padding: 0.7rem;
  padding-left: 3rem;
  font-size: 1.5rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.Sidebar button.logout-btn svg {
  color: rgb(27, 27, 75);
  font-size: 2rem;
}

@media (max-width: 720px) {
  .Sidebar {
    padding: 1rem 0;
    width: 7rem;
    align-items: center;
  }
  .Sidebar .head .user-details {
    display: none;
  }
  .Sidebar button.new-project-btn {
    padding: 1rem;
    width: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .Sidebar button.new-project-btn span {
    display: none;
  }
  .Sidebar nav a span {
    display: none;
  }
  .Sidebar button.logout-btn {
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .Sidebar button.logout-btn span {
    display: none;
  }
}
